import React, { useState, useEffect, useRef } from "react";
import { Box, Container, Grid, LinearProgress, Typography, keyframes, styled, useMediaQuery } from "@mui/material";
import { IoMdClose } from "react-icons/io";
import { IoVolumeMediumOutline } from "react-icons/io5";
import { GoShareAndroid } from "react-icons/go";
import { IoChevronBackCircle } from "react-icons/io5";
import { IoChevronForwardCircle } from "react-icons/io5";
import { handleSpeak } from "src/utils";
import { useLocation, useNavigate } from "react-router-dom";
import ApiConfig from "src/config/APICongig";
import axios from "axios";
import toast from "react-hot-toast";
import { FaAngleDoubleUp } from "react-icons/fa";
import { FaAngleDoubleDown } from "react-icons/fa";
import { useTheme } from "@emotion/react";


const bottomToTop = keyframes`
  0% {
    background-position: bottom;
  }
  100% {
    background-position: top;
  }
`;
const bounce = keyframes`
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-10px); /* Adjust this value to control the bounce height */
  }
`;
const style = {
  flexBox: {
    display: "flex",
    justifyContent: "space-between",
    alignItem: "center",

    "@media(max-width:1000px)": {},
    "@media(max-width:767px)": {},
  },
  gridBox: {
    display: "grid",
    gap: "16px",
  },
  logoBox: {
    height: "-webkit-fill-available",
    alignItems: "end",
    display: "grid",
    justifyContent: "start",
    paddingBottom: {
      md: "0px",
      sm: "90px",
      xs: "90px"
    },
  },
  CombineBox: {
    display: "grid",
    paddingBottom: "20px",
    alignItems: "flex-start",

    height: {
      md: "calc(100vh - 130px)",
      sm: "calc(100vh - 125px)",
      xs: "calc(100vh - 125px)"
    },

  },
  buttonHandle: {
    display: "flex",
    justifyContent: "end",
    gap: "8px",
  },
};
const MainBox = styled(Box)(({ theme }) => ({
  padding: "60px 0px 0 0px",
  [theme.breakpoints.down("md")]: {
    padding: "36px 0px 0 0px",
  },
  [theme.breakpoints.down("sm")]: {
    padding: "36px 0px 0 0px",
  },
  [theme.breakpoints.down("xs")]: {
    padding: "36px 0px 0 0px",
  },
  height: "100vh",
  overflow: "auto",
  alignItems: "end",
  justifyContent: "space-between",
  alignContent: "space-between",

}));
const InnerBox = styled(Box)(({ theme }) => ({
  
  
}));
const AddImg = styled("img")(({ theme }) => ({
  width: "100%",
  maxHeight: "400px",
  "@media(max-width:900px)": {
    maxHeight: "300px",
  },
}));
const SchoolLogo = styled("img")(({ theme }) => ({
  width: "60px",
  height: "60px",
}));
const CustomLinearProgress = styled(LinearProgress)(({ progressColor }) => ({
  width: "-webkit-fill-available",
  height: "10px",
  backgroundColor: "rgba(216, 216, 218, 1)",
  borderRadius: "4px",
  "& .MuiLinearProgress-bar": {
    backgroundColor: "rgba(255, 255, 255, 1)",
    borderRadius: "4px",
  },
}));


function Leason(props) {
  const navigate = useNavigate();
  let min = 1;

  const [progress, setProgress] = useState(1);
  const location = useLocation();
  const [leasonData, setLeasonData] = useState([]);
  const [max, setMax] = useState(leasonData.length);
  const [animationTrigger, setAnimationTrigger] = useState(false);
  const boxRef = useRef(null);
  const [startY, setStartY] = useState(0);
  const [direction, setDirection] = useState("UP");

  useEffect(() => {
    getleasonData();
  }, [])
  const getleasonData = async () => {
    const token = localStorage.getItem("token");
    try {
      const res = await axios({
        method: "GET",
        url: `${ApiConfig.getLeason}/${location?.state?.level_id}/${location?.state?.module_id}`,
        headers: { token: token },
        // params:{
        //   level_id: location?.state?.level_id,
        //   module_id: location?.state?.module_id
        // }
      });
      if (res.status === 200) {
        setLeasonData(res.data.result)
        setMax(res.data.result.length)
      }
    } catch (error) {
      console.log(error, "error");
    }
  };
  const increaseProgress = () => {
    setProgress((prev) => (prev < max ? prev + 1 : max));
    handleSpeak("  ")
    setAnimationTrigger(true);

  };

  const decreaseProgress = () => {
    setProgress((prev) => (prev > min ? prev - 1 : min));
    setAnimationTrigger(true);

  };

  const calculateProgressValue = () => ((progress - min) / (max - min)) * 100;

 


  useEffect(() => {
    if (animationTrigger) {
      const timer = setTimeout(() => {
        setAnimationTrigger(false);
      }, 1000); // Duration of the animation
      return () => clearTimeout(timer);
    }
  }, [animationTrigger]);

  const getBackground = (progress) => {
    switch (progress) {
      case 3:
        return 'rgba(255, 246, 200, 1)';
      case 2:
        return 'rgba(240, 220, 255, 1)';
      default:
        return 'rgba(255, 220, 234, 1)';
    }
  };
//swipe animation
  const handleStart = (clientY) => {
    setStartY(clientY);
    document.addEventListener('mousemove', handleMouseMove);
    document.addEventListener('mouseup', handleMouseUp);
    document.addEventListener('touchmove', handleTouchMove);
    document.addEventListener('touchend', handleTouchEnd);
  };

  const handleMove = (clientY) => {
    const newY = clientY - startY;
    if (boxRef.current) {
      boxRef.current.style.transform = `translateY(${newY}px)`;
    }
  };

  const handleEnd = (clientY) => {
    const deltaY = clientY - startY;
    setDirection(deltaY > 0 ? 'DOWN' : 'UP');
if(deltaY > 0){
  decreaseProgress();
}else{
  increaseProgress();
  if (progress === max) {
    navigate("/take-quiz", {
      state: {
        module_id: location?.state?.level_id,
        level_id: location?.state?.module_id,
      },
    });
  }
}
    document.removeEventListener('mousemove', handleMouseMove);
    document.removeEventListener('mouseup', handleMouseUp);
    document.removeEventListener('touchmove', handleTouchMove);
    document.removeEventListener('touchend', handleTouchEnd);

    // Smoothly reset the box position
    if (boxRef.current) {
      boxRef.current.style.transition = 'transform 0.3s';
      boxRef.current.style.transform = 'translateY(0px)';
      setTimeout(() => {
        if (boxRef.current) {
          boxRef.current.style.transition = '';
        }
      }, 300);
    }
  };
  const theme = useTheme();
  const isDesktop = useMediaQuery(theme.breakpoints.down('md'));
//start
  const handleMouseDown = (e) => {
    if (isDesktop){
       handleStart(e.clientY);
    }
   
  };

  const handleMouseMove = (e) => {
    handleMove(e.clientY);
  };

  const handleMouseUp = (e) => {
    handleEnd(e.clientY);
  };
//start
  const handleTouchStart = (e) => {
    if (isDesktop) {
      handleStart(e.touches[0].clientY);
    }
    
  };

  const handleTouchMove = (e) => {
    handleMove(e.touches[0].clientY);
  };

  const handleTouchEnd = (e) => {
    handleEnd(e.changedTouches[0].clientY);
  };
  return (
    <MainBox
      sx={{
        background: getBackground(progress),
        backgroundSize: '100% 200%',
        backgroundPosition: 'bottom',
        animation: animationTrigger ? `${bottomToTop} 1s forwards` : 'none',
        transition: 'background 1s',// Smooth transition effect,
      }}
      
    >
      <Container maxWidth="lg">
        <Grid container >
          <Grid item xs={12} sm={12} md={8}>
            <Box sx={style.CombineBox}>
              <Box sx={style.gridBox}>
                <Box sx={style.flexBox}>
                  <IoMdClose
                    color="rgba(0, 0, 0, 1)"
                    onClick={() => {
                      navigate("/dashboard");
                    }}
                    cursor={"pointer"}
                  />
                  {/* <Box
                    sx={{ display: "flex", gap: "16px", alignItems: "center" }}
                  >
                    <IoVolumeMediumOutline
                      cursor={"pointer"}
                      color="rgba(0, 0, 0, 1)"
                      onClick={() => {
                        handleSpeak(leasonData[progress - 1]?.name + ". " + leasonData[progress - 1]?.description);
                      }}
                    />
                    <GoShareAndroid color="rgba(0, 0, 0, 1)" />
                  </Box> */}
                </Box>
                <Box sx={{}}>
                  <Typography variant="h1">{leasonData[progress - 1]?.name || "--"}</Typography>
                  <Typography variant="h4" sx={{ marginTop: "14px" }}>
                    {leasonData[progress - 1]?.description || "--"}
                  </Typography>
                </Box>
              </Box>
              <Box sx={style.logoBox}>
                <Box
                  sx={{ display: "flex", alignItems: "start", gap: "15px" }}
                >
                  <SchoolLogo alt="#" src="images/schoolLogo.png" />
                  <Box>
                    <Typography variant="body2">
                      Pravara Public School
                    </Typography>
                    <Typography variant="body2">Pravaranagar</Typography>
                  </Box>
                </Box>
              </Box>
            </Box>
          </Grid>
          <Grid item md={4} sm={12} xs={12}>
            <Box sx={{ background: "rgba(255, 255, 255, 1)" }}>
              <Box sx={{
                display: {
                  md: "block",
                  sm: "none",
                  xs: "none"
                }
              }}>
                <AddImg alt="" src="images/add.png" />
              </Box>
              <Box sx={{
                display: {
                  md: "none",
                  sm: "flex",
                  xs: "flex"
                },
                position: {
                  md: "relative",
                  sm: "fixed",
                  xs: "fixed"
                },
                bottom: {
                  md: "",
                  sm: "54px",
                  xs: "54px"
                },
                width: {
                  md: "auto",
                  sm: "-webkit-fill-available",
                  xs: "-webkit-fill-available"
                }   ,
                justifyContent: "center",
                marginBottom: "5px",
                background: getBackground(progress),
                backgroundSize: '100% 200%',
                backgroundPosition: 'bottom',
                animation: animationTrigger ? `${bottomToTop} 1s forwards` : 'none',
                transition: 'background 1s',// Smooth transition effect
              }}
                
              >
                <Box sx={{
                  animation: `${bounce} 1s infinite`, // Infinite bouncing animation
                 
                }}
                  // ref={boxRef}
                  // onMouseDown={handleMouseDown}
                  // onTouchStart={handleTouchStart}
                 
                >
                  {direction === 'UP' ? <FaAngleDoubleUp
                    style={
                      progress === 3
                        ? { color: "rgba(232, 215, 124, 1)" }
                        : progress === 2
                          ? { color: "rgba(222, 179, 255, 1)" }
                          : { color: "rgba(255, 179, 209, 1)" }
                    }
                    onClick={()=>{increaseProgress()
                      if (progress === max) {
                        navigate("/take-quiz", {
                          state: {
                            module_id: location?.state?.level_id,
                            level_id: location?.state?.module_id,
                          },
                        });
                      }
                    }}
                  /> : <FaAngleDoubleDown style={
                    progress === 3
                      ? { color: "rgba(232, 215, 124, 1)" }
                      : progress === 2
                        ? { color: "rgba(222, 179, 255, 1)" }
                        : { color: "rgba(255, 179, 209, 1)" }
                  } />}
               
                </Box>
               
              </Box>
            </Box>
          </Grid>
        </Grid>
      </Container>
      <InnerBox

        sx={{
            padding: {
              md: "10px 25px",
            sm: "20px 25px",
            xs: "20px 25px"
            },
            position: {
              md: "relative",
              sm: "fixed",
              xs: "fixed"
            },
            bottom: {
              md: "",
              sm: "0",
              xs: "0"
            },
            width: {
              md: "auto",
              sm: "-webkit-fill-available",
              xs: "-webkit-fill-available"
            },      
          
        }}
        // ref={boxRef}
        // onMouseDown={handleMouseDown}
        // onTouchStart={handleTouchStart}
        style={
          progress === 3
            ? { background: "rgba(232, 215, 124, 1)" }
            : progress === 2
              ? { background: "rgba(222, 179, 255, 1)" }
              : { background: "rgba(255, 179, 209, 1)" }
        }
      >
        <Container>
          <Grid container>
            <Grid item xs={12} sm={12} md={8} sx={{ alignItems: "center", display: "grid" }}>
              <Box
                sx={{
                  width: "100%",
                  textAlign: "center",
                  display: "flex",
                  gap: "10px",
                  alignItems: "center",
                }}
                
              >
                <Box sx={{
                  display: {
                    md: "none",
                    sm: "grid",
                    xs: "grid"
                  },
                  alignItems: "center",
                }}>
                  <IoChevronBackCircle style={
                   { color:"#fff"}
                  } onClick={decreaseProgress}/>
                </Box>
                <CustomLinearProgress
                  variant="determinate"
                  value={calculateProgressValue()}
                />
                <Typography variant="body2" color={"#fff"}>
                  {progress}/{max}
                </Typography>
              </Box>
            </Grid>
            <Grid item md={4} sx={{
              display: {
                md: "block",
                sm: "none",
                xs: "none"
              }
            }}>
              <Box sx={{
                display: "flex",
                justifyContent: "end",
                gap: "8px",
}}>
                <IoChevronBackCircle
                  onClick={decreaseProgress}
                  disabled={progress <= min}
                  color="rgba(255, 255, 255, 1)"
                  fontSize={"48px"}

                />

                <IoChevronForwardCircle
                  onClick={() => {
                    if (max == 0) {

                    } else {
                      increaseProgress();
                      if (progress === max) {
                        navigate("/take-quiz", {
                          state: {
                            module_id: location?.state?.level_id,
                            level_id: location?.state?.module_id,
                          },
                        });
                      }

                    }
                  }}
                  disabled={progress >= max}
                  color="rgba(255, 255, 255, 1)"
                  fontSize={"48px"}

                />
              </Box>
            </Grid>
          </Grid>
        </Container>
      </InnerBox>
    </MainBox>
  );
}

export default Leason;
